* {
  box-sizing: border-box;
}

body, header, nav, a, img, ul, li, p, article {
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.header {
  box-shadow: 0 2px 4px #0000000d;
}

.header__content {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  min-height: 60px;
  margin: 0 auto;
  padding-inline: 50px;
  display: flex;
}

.logo-link {
  margin-right: 20px;
  font-size: 0;
}

.nav {
  display: none;
}

@media (width >= 768px) {
  .nav {
    display: block;
  }
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  text-align: center;
  align-items: center;
  height: 60px;
  font-size: 11px;
  font-weight: 500;
  text-decoration: none;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

@media (width >= 1024px) {
  .nav__link {
    font-size: 12px;
  }
}

.nav__link.is-active {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.burger-menu {
  background-image: url("burger-menu.1cd0b7d5.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto;
  width: 50px;
  height: 50px;
  display: block;
}

@media (width >= 768px) {
  .burger-menu {
    display: none;
  }
}

.main {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.stars {
  display: flex;
}

.stars--1 :nth-child(-n+1), .stars--2 :nth-child(-n+2), .stars--3 :nth-child(-n+3), .stars--4 :nth-child(-n+4), .stars--5 :nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.card {
