  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 11px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition-duration: .3s;
  transition-timing-function: ease-in-out;
  display: flex;
}

.card__btn:hover {
  color: #00acdc;
  background-color: #fff;
}

